import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { AlertController, ModalController } from '@ionic/angular';
import { Order } from 'src/app/models/course.interface';
import { AuthService } from 'src/app/services/auth/auth.service';
import { CommonService } from 'src/app/services/common/common.service';
import { DataService } from 'src/app/services/data/data.service';
import { StateService } from 'src/app/services/state/state.service';
import { PaymentInfoPage } from '../payment-info/payment-info.page';

@Component({
  selector: 'app-orders',
  templateUrl: './orders.page.html',
  styleUrls: ['./orders.page.scss'],
})
export class OrdersPage implements OnInit {
  public orders: Order[] = [];

  constructor(
    private dataService: DataService,
    private cd: ChangeDetectorRef,
    private modalCtrl: ModalController,
    private commonService: CommonService,
    private stateService: StateService,
    private authService: AuthService
  ) {
    this.dataService.getOrders().subscribe(res => {
      this.orders = res;
      console.log('orders:::::::::', this.orders);
      this.cd.detectChanges();
    });
  }

  ngOnInit(): void { }

  release(order: Order) {
    this.dataService.updateOrder({ status: true, id: order.id }).then((resp: any) => {
      this.commonService.presentToast('Updated the payment status.', 'primary');
    }, err => {
      console.error('Error while adding the test:::::\n', err);
      this.commonService.presentToast('Unable to update order', 'danger');
    });
  }

  openScreenshot(payment_id: string) {
    this.modalCtrl.create({
      component: PaymentInfoPage,
      componentProps: { payment_id },
    }).then((modal) => {
      modal.present();
    });
  }

  logout() {
    this.authService.logout();
  }
}
